import ListRecipesView from '../views/ListRecipesView.js';
import RecipeDetailView from '../views/RecipeDetailView.js';
import SearchRecipesView from '../views/SearchRecipesView.js';
import CreateRecipeButtonView from '../views/CreateRecipeButtonView.js';
import LoadingView from '../views/LoadingView.js';
import Controller from '../core/Controller.js';
import Recipe from '../models/Recipe.js';
import VIEW_IDS from '../constants/views.js';

class RecipeController extends Controller {
  constructor() {
    super();
    // Initialize properties
    this.listRecipes();
    this.showRecipe();
    this.showSearchRecipesForm();
    this.createRecipeButton();

    this.triggerEvent('hashchange', this.showRecipe.bind(this));
  }

  async listRecipes() {
    const loadingView = new LoadingView(VIEW_IDS.LIST_RECIPES);

    loadingView.render();

    const recipes = await Recipe.searchRecipes(this.queryParams.q);

    // List all recipes
    const view = new ListRecipesView();

    view.render(recipes);
  }

  async showRecipe() {
    const loadingView = new LoadingView(VIEW_IDS.RECIPE_DETAIL);

    loadingView.render();

    const recipe = await Recipe.getRecipe(this.hashParams[0]);

    const view = new RecipeDetailView();

    view.render(recipe);
  }

  showSearchRecipesForm() {
    const view = new SearchRecipesView();

    view.render(this.queryParams);
  }

  createRecipeButton() {
    const view = new CreateRecipeButtonView();

    view.render();
  }

  showCreateRecipeForm() {
    // Logic for showing the create recipe form
  }
}

export default new RecipeController();
