import View from '../core/View.js';
import VIEW_IDS from '../constants/views.js';

class ListRecipesView extends View {
  constructor() {
    super(VIEW_IDS.LIST_RECIPES);
  }

  render(recipes) {
    if (recipes.length === 0) {
      this.addMarkup(`
        <p>No recipes found.</p>
      `);
    } else {
      this.addMarkup(`
        <ul class="recipe-list">
          ${recipes
            .map(
              recipe =>
                `<li class="recipe-item"><a href="#${recipe.id}">${recipe.title}</a></li>`
            )
            .join('')}
        </ul>
    `);
    }
  }
}

export default ListRecipesView;
