class Api {
  #baseUrl;
  #baseHeaders;

  constructor() {
    this.#baseUrl = 'https://forkify-api.herokuapp.com/api/v2/';
    this.#baseHeaders = {
      'Content-Type': 'application/json',
    };
  }

  setBaseUrl(url) {
    this.#baseUrl = url;
  }

  async fetch({ method = 'GET', path, params, queryParams, headers = {} }) {
    const defaultQueryParams = {
      key: '957b9207-ee11-4c5f-a898-714faea9090c',
    };
    const queryString = queryParams
      ? new URLSearchParams({
          ...defaultQueryParams,
          ...queryParams,
        }).toString()
      : new URLSearchParams({ ...defaultQueryParams }).toString();

    try {
      const response = await fetch(`${this.#baseUrl}${path}?${queryString}`, {
        method,
        headers: {
          ...this.#baseHeaders,
          ...headers,
        },
        body: params ? JSON.stringify(params) : null,
      });

      if (!response.ok) throw new Error('Failed to fetch recipes');

      const data = await response.json();

      return data;
    } catch (error) {
      console.error(error);
      return null;
    }
  }

  async get({ queryParams, path, headers }) {
    return await this.fetch({
      method: 'GET',
      path,
      queryParams,
      headers,
    });
  }

  async post({ path, params, headers }) {
    return await this.fetch({
      method: 'POST',
      path,
      params,
      headers,
    });
  }

  async delete({ path, params, headers }) {
    return await this.fetch({
      method: 'DELETE',
      path,
      headers,
      params,
    });
  }
}

export default Api;
