import View from '../core/View.js';
import Recipe from '../models/Recipe.js';

class DeleteRecipeButtonView extends View {
  #formId = 'delete-recipe-form';

  constructor(viewId = 'delete-recipe-button') {
    super(viewId);
  }

  bindEvents() {
    this.addEventListenersToElement(
      this.#formId,
      'submit',
      this.deleteRecipe.bind(this)
    );
  }

  render() {}

  generate(recipeId) {
    return `
      <form id="${this.#formId}">
        <input type="hidden" name="recipeId" value="${recipeId}">
        <button type="submit">Delete This Recipe</button>
      </form>
    `;
  }

  async deleteRecipe(event) {
    event.preventDefault();

    const formData = new FormData(event.target);
    const recipeId = formData.get('recipeId');

    await Recipe.delete(recipeId);

    location.hash = '';

    location.reload();
  }
}

export default DeleteRecipeButtonView;
