import View from '../core/View.js';
import VIEW_IDS from '../constants/views.js';
import DeleteRecipeButtonView from './DeleteRecipeButtonView.js';

class RecipeDetailView extends View {
  constructor() {
    super(VIEW_IDS.RECIPE_DETAIL);
  }

  render(recipe) {
    if (!recipe) {
      this.addMarkup('<p>No recipe</p>');
      return;
    }

    const nestedView = new DeleteRecipeButtonView();

    this.addMarkup(`
        <div class="recipe-detail">
          <h2 class="recipe-title">${recipe.title}</h2>
          <img src="${recipe.thumbnail}" alt="${recipe.title}" />
          <p class="recipe-description">${recipe.author}</p>
          ${nestedView.generate(recipe.id)}
        </div>
    `);

    nestedView.bindEvents();
  }
}

export default RecipeDetailView;
