import View from '../core/View.js';
import VIEW_IDS from '../constants/views.js';

class SearchRecipesView extends View {
  #formId = 'search-recipes-form';

  constructor() {
    super(VIEW_IDS.SEARCH_RECIPES);
  }

  render(queryParams) {
    this.addMarkup(`
        <form id="${this.#formId}">
            <input type="text" name="q" placeholder="Search for recipes..." value="${
              queryParams.q || ''
            }" />
        </form>
    `);

    this.addEventListenersToElement(
      this.#formId,
      'submit',
      this.handleSearch.bind(this)
    );
  }

  handleSearch(event) {
    location.hash = '';
  }
}

export default SearchRecipesView;
