import Model from '../core/Model.js';

class Recipe extends Model {
  id;
  title;
  thumbnail;
  author;

  constructor() {
    super();
  }

  transform({ publisher, image_url, id, title }) {
    this.id = id;
    this.title = title;
    this.thumbnail = image_url;
    this.author = publisher;

    return this;
  }

  async listRecipes() {
    const response = await this.api.get({
      path: 'recipes',
      queryParams: { search: 'pizza' },
    });

    return response
      ? response.data.recipes.map(recipe => new Recipe().transform(recipe))
      : [];
  }

  async getRecipe(id) {
    if (!id) return null;

    const response = await this.api.get({
      path: `recipes/${id}`,
    });

    return response ? new Recipe().transform(response.data.recipe) : null;
  }

  async searchRecipes(query = 'pizza') {
    const response = await this.api.get({
      path: 'recipes',
      queryParams: { search: query },
    });

    return response
      ? response.data.recipes.map(recipe => new Recipe().transform(recipe))
      : [];
  }

  async create(data) {
    const response = await this.api.post({
      path: 'recipes',
      params: {
        title: 'Pizza Ky',
        publisher: 'Ky123',
        image_url: 'https://example.com/pizza.jpg',
        source_url: 'https://example.com/pizza-recipe',
        cooking_time: 30,
        servings: 4,
      },
    });
    return response ? new Recipe().transform(response.data.recipe) : null;
  }

  async delete(id) {
    if (!id) return null;

    const response = await this.api.delete({
      path: `recipes/${id}`,
    });

    return response;
  }
}

export default new Recipe();
